<template>
  <div>
    <div class="art-lis" v-for="(art, index) in localData" :key="index">
      <!--  -->
      <h3 class="art-title">{{art.articleTitle}}</h3>
      <!--  -->
      <p class="art-content">{{art.articleContent}}</p>
      <!--  -->
      <div class="art-icon">
        <span class="heat-icon">
          <i class="iconfont icon-icon" title=""></i>
        </span>
        <span class="article-heat">{{art.articleHeat}}</span>
        <span class="creat-time">
          <i class="iconfont icon-dingdanxiangqing-chuangjianshijian" title=""></i> {{art.createTime}}
        </span>
        <span class="update-time">
          <i class="iconfont icon-gengxinshijian" title=""></i>{{art.updateTime}}
        </span>
      </div>
      <div class="underline"></div>
    </div>
  </div>
</template>
<script>
//ajax
import { ajax } from "../network/request";
import qs from 'qs';
export default {
  data() {
    return {
      //
      localData:[]
    };
  },
  computed: {
    userName() {
      return this.$store.state.userName;
    },
    token(){
      return this.$store.state.token;
    }
  },
  created() {
    let data = {
      userName:this.userName
    }
    ajax({
      url: "/upload/homeArticle",
      method: "post",
      data: qs.stringify(data),
      //token
      headers:{'token':this.token}
    })
      .then(res => {
        
        if (res.status == 0) {
          //
          localData = res.data;
        }
      })
      .catch(err => {
        alert("");
        console.log(err);
      });
  }
};
</script>
<style scoped>
.art-lis {
  margin-left: 120px;
  margin-top: 30px;
  width: 730px;
}
.art-content {
  line-height: 30px;
  color: #999;
  font-size: 13px;
  margin-bottom: 20px;
}
.underline {
  height: 1px;
  background-color: #f0f0f0;
  width: 730px;
  box-shadow: 0px -1px 1px 0px #fbfbfb;
}
.art-title {
  cursor: pointer;
  font-family: -apple-system, SF UI Display, Arial, PingFang SC,
    Hiragino Sans GB, Microsoft YaHei, WenQuanYi Micro Hei, sans-serif;
  margin-bottom: 5px;
}
.art-title:hover {
  text-decoration: underline;
}
.art-icon {
  color: #b4b4b4;
  font-size: 12px;
}
.art-icon i {
  margin-left: 8px;
  font-size: 11px;
}
.heat-icon i {
  color: #ea6f5a;
}
</style>


